@import '../../scss';

:host {
  display: block;
  position: fixed;
  top: 0;
  left: -1000px;
  height: 100vh;
  width: 100vw;
  z-index: $phn-z-index-mega-flyout + 1;

  &.slide-in-from-left {
    position: absolute;
    animation: slide-in-from-left-absolute 600ms;
  }

  &.slide-out-to-left {
    position: absolute;
    animation: slide-in-from-left-absolute 600ms reverse;
  }

  .drawer {
    z-index: $phn-z-index-mega-flyout + 1;
    background-color: $pds-theme-light-background-base;
    position: absolute;
    left: 0;

    .drawer-content {
      overflow-y: auto;
      height: 100vh;
      width: 100vw;
      box-sizing: border-box;
      padding: 0 p-px-to-rem(27px) p-px-to-rem(100px) p-px-to-rem(27px);

      @include pds-media-query-min('xs') {
        padding: 0 p-px-to-rem(35px) p-px-to-rem(100px) p-px-to-rem(35px);
      }

      @include pds-media-query-min('s') {
        padding: 0 p-px-to-rem(54px) p-px-to-rem(100px) p-px-to-rem(54px);
      }

      @include pds-media-query-min('m') {
        padding: 0 p-px-to-rem(36px) p-px-to-rem(100px) p-px-to-rem(36px);
      }

      phn-drawer-header {
        box-sizing: border-box;
        margin-top: p-px-to-rem(18px);
        margin-bottom: p-px-to-rem(31px);

        @include pds-media-query-min('xs') {
          margin-top: p-px-to-rem(32px);
          margin-bottom: p-px-to-rem(37px);
        }

        @include pds-media-query-min('s') {
          margin-bottom: p-px-to-rem(20px);
        }
      }

      phn-router {
        padding-bottom: 4rem;
      }
    }

    .bottom-gradient{
      @include drawer-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255));
      margin-left: 0;
      z-index: 1;
    }
  }

}
