import { Fragment, h, JSX } from '@stencil/core';
import { getBackButtonText } from '../../services/menu-items-service';
import { navContentStore } from '../../state/nav-content-store';

export default function DrawerContent({
  routingKey,
  previousRoutingKey,
  breakpoint,
  locale,
  showBackButton,
  receivedContent
}: {
  routingKey: string;
  previousRoutingKey: string;
  breakpoint: number;
  locale: string;
  showBackButton: boolean;
  receivedContent: boolean;
}): JSX.Element {
  const { menu } = navContentStore.state;
  const backButtonText = getBackButtonText(navContentStore.state, previousRoutingKey ?? '') || menu.text;

  return (
    <Fragment>
      <div class="bottom-gradient"></div>
      <div class="drawer-content">
        <phn-drawer-header
          showBackButton={showBackButton}
          backButtonAnalyticsId={menu.id}
          backButtonText={backButtonText}
          activeRoutingKey={routingKey}
          breakpoint={breakpoint}
          receivedContent={receivedContent}
          locale={locale}
        />
        <phn-router
          activeRoutingKey={routingKey}
          breakpoint={breakpoint}
          locale={locale}
          receivedContent={receivedContent}
        />
      </div>
    </Fragment>
  );
}
